@import "react-redux-toastr/src/styles/index";
@import "./colors";
@import "./reset";
@import "./fonts";

.full-screen {
  width: 100%;
  height: 100%; }

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

body {
  font-family: 'Raleway', sans-serif; }

.button-unstyled {
  background: none;
  border: 0;
  color: inherit;
  font: inherit;
  margin: 0;
  line-height: inherit;
  overflow: initial;
  padding: 0;
  text-align: initial;
  vertical-align: initial;
  cursor: pointer; }

.tox-toolbar__group:last-child {
  margin-left: auto;
  border-left: none;

  .tox-tbtn {
    flex-direction: row-reverse; } }
